import {
  convertFullToHalf,
  convertFractionToDecimal,
  convertEllipsisToSpace,
  convertRadicalToKanji,
  removeSymbols,
  removeExtraSpaces,
} from "../index";

// 
export default function divideInput(text, isRemoveSymbolsApplied) {
  const lines = text
    .split("\n")
    .map((line) => convertFullToHalf(line)) // 
    .map((line) => convertRadicalToKanji(line)) // 
    .map((line) => convertEllipsisToSpace(line)) // 
    .map((line) => (isRemoveSymbolsApplied ? removeSymbols(line) : line)) // 
    .map((line) => removeExtraSpaces(line)) // 
    .filter((line) => line.trim()) // 
    .map((line) => convertFractionToDecimal(line)) // 
    .map((line) => line.trim()); // 

  return lines;
}
