const SpoonToGramModal = ({ isOpen, onClose }) => {
  return (
    <Modal
      isOpen={isOpen("spoonToGram")}
      onClose={() => onClose("spoonToGram")}
      title="  g "
    >
      <div className="modal-content">
        <p>
          
        </p>
        <div className="border">
          (1)
          <br />
           1 18g
          <br />
           2 8g
        </div>
        <p></p>
        <div className="border">
          (2)
          <br />
            2 36g
          <br />
            12g
          <br />
              2  36g
        </div>
        <p>
          
          <br />
          
        </p>
        <TeaspoonTable />
      </div>
    </Modal>
  );
};

export default SpoonToGramModal;
