import { useState } from "react";

const useCartCountSpinButton = ({ cartCount, hadleCartCountChange }) => {
  const maxCount = 1000;
  const [value, setValue] = useState(cartCount);
  const [errorMessage, setErrorMessage] = useState("");
  // 
  const [isComposing, setIsComposing] = useState(false);

  const handleCountUp = () => {
    setValue((prevCount) => {
      const newCount = Math.min(prevCount + 1, maxCount);
      hadleCartCountChange(newCount);
      return newCount;
    });
  };

  const handleCountDown = () => {
    setValue((prevCount) => {
      const newCount = Math.max(0, prevCount - 1);
      hadleCartCountChange(newCount);
      return newCount;
    });
  };

  const handleCountChange = (event) => {
    const inputValue = event.target.value;
    setValue(inputValue);
  };

  // 
  const handleBlur = () => {
    const inputNumber = Number(value);
    if (value === "") {
      setValue(0);
      hadleCartCountChange(0);
    } else if (isNaN(inputNumber) || inputNumber < 0) {
      setValue(0);
      hadleCartCountChange(0);
      setErrorMessage("");
    } else if (inputNumber > maxCount) {
      setValue(maxCount);
      hadleCartCountChange(maxCount);
      setErrorMessage(`${maxCount}`);
    } else {
      setValue(inputNumber);
      hadleCartCountChange(inputNumber);
    }

    setTimeout(() => {
      setErrorMessage("");
    }, 2000);
  };

  // Enter
  const handleKeyDown = (event) => {
    if (event.key === "Enter" && !isComposing) {
      handleBlur();
      event.target.blur();
    }
  };

  // 
  const isMinCount = () => {
    return Number(value) <= 0;
  };

  // 
  const isMaxCount = () => {
    return Number(value) >= maxCount;
  };

  return {
    handleCountUp,
    handleCountDown,
    handleCountChange,
    handleBlur,
    handleKeyDown,
    setIsComposing,
    isMinCount,
    isMaxCount,
    value,
    errorMessage,
  };
};

export default useCartCountSpinButton;
