import React from "react";
import "./InputFormatTag.scss";

const InputFormatTag = ({ inputFormat, onClick }) => {
  // inputFormat
  const getTagLabel = () => {
    switch (inputFormat) {
      case "not-entered":
        return "";
      case "one-line":
        return "11";
      case "two-line":
        return "21";
      default:
        return "";
    }
  };

  return (
    <div>
      <div className={`inputFormatTag ${inputFormat}`} onClick={onClick}>
        {getTagLabel()}
      </div>
    </div>
  );
};

export default InputFormatTag;
