import { useState } from "react";

const useCopyButton = ({ text }) => {
  // 
  const [copyStatus, setCopyStatus] = useState("");

  const handleCopy = () => {
    navigator.clipboard
      .writeText(text)
      .then(() => {
        // 
        setCopyStatus("");
        // 2.5
        setTimeout(() => setCopyStatus(""), 2500);
      })
      .catch((err) => {
        console.error(":", err);
      });
  };

  return { copyStatus, handleCopy };
};

export default useCopyButton;