// 
export default function classifyInputFormat (lines) {
  let currentFormat = "";
  if (lines.length === 0) {
    currentFormat = "not-entered";
  } else {
    // "two-line"
    const isTwoLine = lines.some((line) => /\d/.test(line) && !/\s/.test(line));
    currentFormat = isTwoLine ? "two-line" : "one-line";
  }

  return currentFormat;
};
