// 
export default function removeExtraSpaces(str) {
  const strWithoutExtraSpaces = str.replace(
    /\s*\(\s*([^)]*?)\s*\)/g,
    (_, insideParentheses) => {
      // 
      const noSpacesInside = insideParentheses.replace(/\s+/g, "");
      return `(${noSpacesInside})`;
    }
  );
  return strWithoutExtraSpaces;
}
